# Embedded Fonts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
  border: 2px solid;
  border-color: #0c0;
  border-radius: 3px;
  background: #222;
  color: #0c0;
  font-size: 0.75rem;
  line-height: 1;
  padding: 0.6rem 2rem 0.4rem; }
  button:hover,
  input[type='button']:hover,
  input[type='reset']:hover,
  input[type='submit']:hover {
    border-color: #ccc #bbb #aaa; }
  button:active, button:focus,
  input[type='button']:active,
  input[type='button']:focus,
  input[type='reset']:active,
  input[type='reset']:focus,
  input[type='submit']:active,
  input[type='submit']:focus {
    border-color: #aaa #bbb #bbb;
    box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15); }

/* Fields */
input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'],
textarea {
  background-color: #222;
  border: 2px solid #0c0;
  border-radius: 3px; }
  input[type='text']:focus,
  input[type='email']:focus,
  input[type='url']:focus,
  input[type='password']:focus,
  input[type='search']:focus,
  textarea:focus {
    color: #111; }

input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'] {
  padding: 0.5em 2em; }
  @media (min-width: 600px) {
    input[type='text'],
    input[type='email'],
    input[type='url'],
    input[type='password'],
    input[type='search'] {
      padding: 0.5em 10em; } }

textarea {
  padding-left: 3px;
  width: 100%; }
  @media (min-width: 600px) {
    textarea {
      width: 130%; } }
  @media (min-width: 1000px) {
    textarea {
      width: 130%; } }

.fa-search {
  border-color: #222; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5rem; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

